import {
    Target, Bot, BarChart3, Workflow, Database, RefreshCw, Shield
} from "lucide-react"

export const metadata = {
    title: "Features | Project Catalyst",
    description: "Explore our AI-powered prospecting, outreach, and analytics tools.",
}

export default function FeaturesPage() {
    const sections = [
        {
            title: "Prospecting Tools",
            description: "Find your perfect MSP clients with precision.",
            items: [
                { icon: Target, title: "AI Lead Scoring", desc: "Predict which prospects are most likely to convert based on 50+ signals." },
                { icon: Database, title: "Technographic Filtering", desc: "Filter by existing tech stack (Office 365, AWS, etc.)" },
                { icon: Shield, title: "Industry Firewall", desc: "Never pitch a competitor. We screen out all MSPs and IT firms from your lists automatically." },
                { icon: RefreshCw, title: "Contact Verification", desc: "Real-time email and phone verification to ensure deliverability." },
            ]
        },
        {
            title: "Outreach Automation",
            description: "Scale your sales engagement without losing the personal touch.",
            items: [
                { icon: Bot, title: "Multi-Channel Campaigns", desc: "Orchestrate sequences across Email, LinkedIn, and Phone." },
                { icon: Workflow, title: "Smart Follow-ups", desc: "Automated chases based on prospect behavior and engagement." },
                { icon: RefreshCw, title: "A/B Testing", desc: "Optimize subject lines and messaging automatically." },
            ]
        },
        {
            title: "Analytics & Reporting",
            description: "Data-driven insights to optimize your pipeline.",
            items: [
                { icon: BarChart3, title: "Pipeline Dashboards", desc: "Visual overview of deal flow and team performance." },
                { icon: BarChart3, title: "ROI Calculator", desc: "Track cost per lead and acquisition costs in real-time." },
                { icon: BarChart3, title: "Conversion Tracking", desc: "Identify bottlenecks in your sales funnel." },
            ]
        },
        {
            title: "Competitive Intelligence",
            description: "Stay ahead of the market with exclusive insights.",
            items: [
                { icon: Shield, title: "Competitor Watch", desc: "Track competitor pricing, packaging, and messaging changes in real-time." },
                { icon: Database, title: "Client-Exclusive Data Feed", desc: "Your data is yours alone. We never feed your prospect interaction data to competing MSPs." },
                { icon: Target, title: "Market Gap Analysis", desc: "Identify underserved verticals in your service area." },
            ]
        }
    ]

    return (
        <div className="py-20">
            <div className="container">
                <div className="text-center max-w-3xl mx-auto mb-20">
                    <h1 className="text-4xl md:text-5xl font-bold mb-6">Platform Features</h1>
                    <p className="text-xl text-muted-foreground">
                        A complete operating system for MSP sales teams.
                    </p>
                </div>

                <div className="space-y-24">
                    {sections.map((section, idx) => (
                        <div key={idx} className="space-y-8">
                            <div className="text-center md:text-left">
                                <h2 className="text-3xl font-bold mb-2">{section.title}</h2>
                                <p className="text-muted-foreground text-lg">{section.description}</p>
                            </div>
                            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                                {section.items.map((item, i) => (
                                    <div key={i} className="p-6 rounded-2xl border bg-card hover:border-primary/50 transition-colors">
                                        <item.icon className="h-10 w-10 text-primary mb-4" />
                                        <h3 className="text-xl font-semibold mb-2">{item.title}</h3>
                                        <p className="text-muted-foreground">{item.desc}</p>
                                    </div>
                                ))}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}
